.promo {
  background-repeat: no-repeat;
  min-height: 734px;
  background-color: #1d2631;
  background-image: url("../img/back-mobile.png");
  background-position: 50% 0;
  background-size: 100%;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/back-mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/tablet-fon.jpg");
    background-size: 1200px 729px;
    min-height: 726px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/tablet-fon@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/desktop-fon.jpg");
    min-height: 853px;
    background-size: 2560px 853px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop-fon@2x.jpg");
      background-size: 2400px 1722px;
    }
  }

  &--inner-page {
    min-height: 238px;

    @media (min-width: $tablet-width) {
      min-height: 438px;
    }

    @media (min-width: $desktop-width) {
      min-height: 394px;
    }
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  min-height: inherit;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    margin: 0 auto 0 auto;
    justify-content: flex-start;
    padding-left: 20px;
    padding-right: 20px;
    width: 660px;
  }

  @media (min-width: $desktop-width) {
    width: 940px;
    padding: 0;
  }
}

.promo__slogan {
  background-color: transparent;
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: white;
  margin: 90px auto 0 auto;
  box-sizing: border-box;
  min-height: inherit;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 120%;
    margin: 93px 0 0 20px;
  }

  @media (min-width: $desktop-width) {
    font-weight: 300;
    font-size: 50px;
    line-height: 68px;
    margin-top: 85px;
    margin-left: 20px;
  }
}

.promo__header {
  padding: 0;
  margin: 0 auto;
  position: relative;
  width: 280px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    margin: 107px 0 0 0;
  }

  @media (min-width: $desktop-width) {
    margin: 123px 0 0 0;
  }

  &::after {
    content: "";
    position: absolute;
    width: 320px;
    height: 286px;
    background-image: url("../img/phone.png");
    background-repeat: no-repeat;
    top: 176px;
    left: -20px;
    box-sizing: border-box;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/phone@2x.png");
      background-size: 320px 286px;
    }

    @media (min-width: $tablet-width) {
      width: 660px;
      height: 545px;
      background-image: url("../img/phone-tablet.png");
      top: 75px;
      left: -20px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/phone-tablet@2x.png");
        background-size: 660px 545px;
      }
    }

    @media (min-width: $desktop-width) {
      width: 1050px;
      height: 645px;
      background-image: url("../img/phone-desktop.png");
      top: 84px;
      left: 16px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/phone-desktop@2x.png");
        background-size: 1050px 645px;
      }
    }
  }
}

.promo__caption {
  margin: 0 20px;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  color: #ffffff;
  padding: 95px 0 0 0;

  @media (min-width: $tablet-width) {
    font-size: 50px;
    line-height: 68px;
    padding: 170px 0 0 0;
  }

  @media (min-width: $desktop-width) {
    padding: 170px 20px 88px 20px;
  }
}

.promo__catalog-caption {
  padding: 95px 20px 0 20px;

  @media (min-width: $tablet-width) {
    padding: 170px 0 0 0;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 167px 0 0 0;
  }
}

.promo__application {
  background-color: transparent;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    padding: 30px 0 0 0;
  }

  @media (min-width: $desktop-width) {
    padding: 37px 0 16px 0;
    width: auto;
    height: auto;
  }
}
